@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@600;800&display=swap);
* {
  padding: 0;
  border: 0;
}

:root {

/* <--Primary--> */

--softCyanFullSliderBar: hsl(174, 77%, 80%);
--StrongCyanSliderBackround: hsl(174, 86%, 45%);
--LightGrayishRedDiscountBackground: hsl(14, 92%, 95%);
--LightRedDiscountText: hsl(15, 100%, 70%);
--PaleBlueCTAText: hsl(226, 100%, 87%);

/* ### Neutral */

--WhitePricingComponentBackground: hsl (0, 0%, 100%);
--VeryPaleBlueMainBackground: hsl(230, 100%, 99%);
--LightGrayishBlueEmptySliderBar: hsl(224, 65%, 95%);
--LightGrayishBlueToggleBackground: hsl(223, 50%, 87%);
--GrayishBlueText: hsl(225, 20%, 60%);
--DarkDesaturatedBlueTextCTABackground: hsl(227, 35%, 25%);
}

html {
  box-sizing: border-box;
  height: auto;
  width: auto;
  overflow-x: hidden;
}

body {
  font-family: "Monrope", Helvetica, sans-serif;
  min-height: 100vh;
  font-size: 15px;
  color: hsl(227, 35%, 25%);
  color: var(--DarkDesaturatedBlueTextCTABackground);
  background: hsl(230, 100%, 99%);
  background: var(--VeryPaleBlueMainBackground);
  overflow-x: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header-wrapper {
  background-image: url(/react-pricing-element/static/media/pattern-circles.9f6ed14b.svg);
  background-repeat: no-repeat;
  background-position-x: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.text-header {
  align-items: center;
  display: flex;
  justify-content: center;
  text-align: center;
  font-size: 24px;
  
}

.sub-text {
  color: hsl(225, 20%, 60%);
  color: var(--GrayishBlueText);
  display: flex;
    justify-content: center;
    flex-direction: column;
    flex-wrap: wrap;
    text-align: center;
    margin: 0 78px 100px;
}
.card-body{
  background: var(--WhitePricingComponentBackground);
  height: 400px;
  align-items: center;
    display: flex;
    justify-content: space-around;
  flex-direction: column;
}

.card {
    border-radius: 7px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
.footer-wrapper {
    display: flex;
    flex-direction: column;
    padding: 10px;
}

.promise-list li{
    list-style-type: none;
    padding: 3px;
    text-align: center;
    color: var(--GrayishBlueText);
}

.promise-list li::before {
    content: url(/react-pricing-element/static/media/icon-check.83c38a48.svg);
    margin-right: 10px;
}

.trial-btn {
    padding: 10px 45px;
    border-radius: 25px;
    background-color: var(--DarkDesaturatedBlueTextCTABackground);
    color: var(--LightGrayishBlueEmptySliderBar);
    text-align: center;
    cursor: pointer;
}



@media (min-width: 600px) {

    .footer-wrapper {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        width: 80%;
    }

}
.toggle-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 80%;
    align-items: center;
}

.discount {
    background-color: var(--LightGrayishRedDiscountBackground);
    color: var(--LightRedDiscountText);
    padding: 5px 13px;
    border-radius: 20px;
    font-size: 12px;
    font-weight: 600;
}

.text {
    color: var(--GrayishBlueText);
    font-size: 12px;
    font-weight: 600;
}

.toggle-switch {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    text-align: left;
}

input[type=checkbox]{
	height: 0;
	width: 0;
	display: none;
}

label {
	cursor: pointer;
	text-indent: -9999px;
	width: 50px;
	height: 25px;
	background: var(--LightGrayishBlueToggleBackground);
	display: block;
	border-radius: 100px;
	position: relative;
}

label:after {
	content: '';
	position: absolute;
	top: 2px;
	left: 2px;
	width: 21px;
	height: 21px;
	background: #fff;
	border-radius: 90px;
	transition: 0.3s;
}


input:checked + label {
	background:var(--StrongCyanSliderBackround);
}

input:checked + label:after {
	left: calc(100% - 2px);
	-webkit-transform: translateX(-100%);
	        transform: translateX(-100%);
}

.slider-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 80%;
}

.page-views {
    color: var(--GrayishBlueText);
    font-size: 18px;
    text-transform:uppercase;
    font-weight: 600;
    order: 1;
    letter-spacing: 2px;
    margin: 20px;
}

.range-slider-value {
    color: var(--DarkDesaturatedBlueTextCTABackground);
    font-size: 20px;
    font-weight: 800;
    margin: 20px;
    order: 3;
}

.range-slider-value span{
    color: var(--GrayishBlueText);
    font-size: 14px;
    padding-left: 10px;
    font-weight: 600;
}

.slider-container {
    order: 2;
    width: 100%;
}


.label-slider {
	height: 0;
	width: 0;
	display: none;
}

.slider{
    -webkit-appearance: none;
    width: 100%;
    height: 10px;
    border-radius: 12px; 
    background:var(--LightGrayishBlueEmptySliderBar);
    
    outline: none;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2) inset;;
} 


.slider::-webkit-slider-thumb {
    background: url(/react-pricing-element/static/media/icon-slider.cbca35e0.svg);
    background-repeat: no-repeat;
    background-color: var(--softCyanFullSliderBar);
    -webkit-appearance: none; /* Override default look */
    appearance: none;
    border-radius: 100%;
    background-position: center;
    width: 30px; /* Set a specific slider handle width */
    height: 30px; /* Slider handle height */
    /* background: #04AA6D; Green background */
    cursor: pointer; /* Cursor on hover */
    -webkit-transition: background .15s ease-in-out;
    transition: background .15s ease-in-out;
}

.slider:active::-webkit-slider-thumb{
    background: var(--StrongCyanSliderBackround);
}


@media (min-width: 600px) {
    .slider-wrapper {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        width: 80%;
        flex-wrap: wrap;
    }
    
    .range-slider-value {
        
        order: 2;
    
    }
    
    .slider-container {
        order: 2;
        width: 100%;
        margin-bottom: 20px;
    }

}


.page-views {
    color: var(--GrayishBlueText);
    font-size: 18px;
    text-transform:uppercase;
    font-weight: 600;
    order: 1;
    letter-spacing: 2px;
    margin: 20px;
}
